"use client";

import { useState } from "react";
import { Calendar, Users, HeartPulse, DollarSign, MessageSquare } from "lucide-react";

import Link from "next/link";

export default function AdminDashboard() {
  const [activePage, setActivePage] = useState("dashboard");

  const handleCardClick = (pageName: string) => {
    setActivePage(pageName);
  };

  const renderCards = () => (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mt-8">
      <div
        onClick={() => setActivePage("Today's Appointments")}
        className="cursor-pointer bg-white p-6 rounded-xl shadow flex items-center space-x-4 hover:bg-blue-100"
      >
        <Calendar className="w-10 h-10 text-blue-600" />
        <div>
          <h3 className="text-xl font-semibold text-gray-800">Today's Appointments</h3>
          <p className="text-gray-600">24 appointments</p>
        </div>
      </div>

      <div
        onClick={() => setActivePage("Doctors")}
        className="cursor-pointer bg-white p-6 rounded-xl shadow flex items-center space-x-4 hover:bg-blue-100"
      >
        <HeartPulse className="w-10 h-10 text-green-600" />
        <div>
          <h3 className="text-xl font-semibold text-gray-800">Doctors</h3>
          <p className="text-gray-600">12 doctors</p>
        </div>
      </div>

      <div
        onClick={() => setActivePage("Total Patients")}
        className="cursor-pointer bg-white p-6 rounded-xl shadow flex items-center space-x-4 hover:bg-blue-100"
      >
        <Users className="w-10 h-10 text-purple-600" />
        <div>
          <h3 className="text-xl font-semibold text-gray-800">Total Patients</h3>
          <p className="text-gray-600">320 patients</p>
        </div>
      </div>

      <div
        onClick={() => setActivePage("Top Booked Department")}
        className="cursor-pointer bg-white p-6 rounded-xl shadow flex items-center space-x-4 hover:bg-blue-100"
      >
        <HeartPulse className="w-10 h-10 text-pink-600" />
        <div>
          <h3 className="text-xl font-semibold text-gray-800">Top Booked Department</h3>
          <p className="text-gray-600">Cardiology</p>
        </div>
      </div>

      <div
        onClick={() => setActivePage("Total Revenue")}
        className="cursor-pointer bg-white p-6 rounded-xl shadow flex items-center space-x-4 hover:bg-blue-100"
      >
        <DollarSign className="w-10 h-10 text-yellow-600" />
        <div>
          <h3 className="text-xl font-semibold text-gray-800">Total Revenue</h3>
          <p className="text-gray-600">$18,500</p>
        </div>
      </div>
    </div>
  );

  return (
    <div>
      {activePage === "dashboard" ? (
        renderCards()
      ) : (
        <div className="mt-12 text-center text-3xl font-bold text-gray-800">{activePage}</div>
      )}
    </div>
  );
}
